import React, { Component } from 'react';
import './Services.css'

class Services extends Component {

    render(){
        return(
        <div className="ServiceContainer">
            <div className="ServiceLogoContainer">
                <div className="logoWrapper">
                    <img src={this.props.logo} className="ServiceLogo"/>
                </div>
                <div className="titleWrapper">
                    <p className="ServiceTitle">{this.props.title}</p>
                </div>
            </div>
            <p className="ServiceSubtitle">Lorem ipsum dolor sit amet, ei sea euripidis philosophia, pri ullum dicit eu, sit in scripta efficiendi. Dicunt inimicus</p>
        </div>
        );
    }

}

export default Services;


