import React, { Component } from 'react';
import Slider from "react-slick";
import './TestimonialCell.css'

class TestimonialCell extends Component {

    render(){
        return(
            <div className="TestimonialContainer">
                <div className="holder">
                    <p className="TTitle">WHAT <span className="bold">OUR CLIENTS</span> ARE SAYING</p>
                    <div className="lineHolder"><div className="line"/></div>
                    <div className="MessageHolder">
                    <img src={require('../../../images/quotation.png')}/>
                    <p>I appreciate lorem ipsum dolor sit amet, ei sea euripidis philosophia, pri ullum dicit eu, sit in scripta efficiendi. Dicunt inimicus. Lorem ipsum dolor sit amet.</p>
                    </div>
                    <div className="ProfileHolder">
                        <img src="https://www.biography.com/.image/ar_1:1%2Cc_fill%2Ccs_srgb%2Cg_face%2Cq_auto:good%2Cw_300/MTE5NDg0MDU0NTIzODQwMDE1/steven-jobs-9354805-2-402.jpg"/>
                        <div className = "clientDetails">
                            <p className="clientName">Steve Jobs</p>
                            <p className="clientJob">CEO, Apple.inc</p>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

}

export default TestimonialCell;


