import React, { Component } from 'react';
import Slider from "react-slick";
import './Testimonial.css'
import TestimonialCell from './testimonialCell/TestimonialCell';

class Testimonial extends Component {

    render(){

        var settings = {
            dots: true,
            infinite: true,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1,
            // fade:true,
            // autoplay: true,
            // autoplaySpeed: 2500,
            cssEase: "linear",
          };


        return(
        <div >
            <Slider {...settings} >
                <TestimonialCell/>
                <TestimonialCell/>
                <TestimonialCell/>
                <TestimonialCell/>
            </Slider>
            <div className="contactButtonWrapper">
                <div className="contactButton">GET IN TOUCH</div>
            </div>
        </div>
        );
    }

}

export default Testimonial;


