import React, { Component } from 'react';
import {Navbar,Nav,NavItem} from 'react-bootstrap'
import './Header.css'


class Header extends Component {
    render() {
      return (
        <div className="header">
            <Navbar className="transparent navbar">
                <Navbar.Header>
                 <Navbar.Brand>
                    <img src ={require('../../images/logo.png')} className="logo"/>
                </Navbar.Brand>
              <Navbar.Toggle />
            </Navbar.Header>
            <Navbar.Collapse>
            <Nav pullRight >
                <NavItem eventKey={1} href="#">
                    <span className="navItem">SEO</span>
                </NavItem>
                <NavItem eventKey={2} href="#">
                    <span className="navItem">UX</span>
                </NavItem>
                <NavItem eventKey={3} href="#">
                    <span className="navItem">WORK</span>
                </NavItem>
                <NavItem eventKey={4} href="#">
                    <span className="navItem">ABOUT</span>
                </NavItem>
                <NavItem eventKey={5} href="#">
                    <span className="navItem">LET'S WORK TOGETHER</span>
                </NavItem>
            </Nav>
            </Navbar.Collapse>
          </Navbar>
          <div className = "titleContainer" >
            <p className="Title">THE BEST SEO AGENCY EVER</p>
            <p className="Subtitle">Your business is always safe in our hands.</p>
          </div>
        </div>
      );
    }
  }
  
  export default Header;


